import React from 'react';
import { ThemeProvider } from 'styled-components';
import 'sanitize.css';

import './global-styles';

import theme from './theme';
import Box from '../Box';
import Text from '../Text';

if (typeof window !== 'undefined') {
  const webfontloader = require('webfontloader');

  webfontloader.load({
    google: {
      families: ['Dosis:200,400,700', 'Montserrat']
    },
  });
}

export default (props) => (
  <ThemeProvider theme={theme}>
    <Box height="100%">
      <Box height="100%" {...props} />
      <Box position="absolute" top="-9999px" left="-9999px">
        <Text.dosis fontWeight={200}>1</Text.dosis>
        <Text.dosis fontWeight={400}>1</Text.dosis>
        <Text.dosis fontWeight={700}>1</Text.dosis>
      </Box>
    </Box>
  </ThemeProvider>
);



