import { injectGlobal } from 'styled-components';

import theme from './theme';
import responsive from './responsive';

injectGlobal`
  html, body {
    width: 100%;
    height: 100%;
  }

  body {
    font-family: ${theme.font};
    color: ${theme.colors.text};
    font-size: 14px;
    ${responsive.sm`
      font-size: 16px;
    `}
  }


  #___gatsby {
    height: 100%;
  }
`;



